@import url(https://fonts.googleapis.com/css?family=Questrial);
@import url(https://fonts.googleapis.com/css?family=Questrial);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: "Questrial";
  margin-bottom: 40px;
}

h2 {
  font-size: 2em !important;
  color: #6fb9a8 !important;
  font-weight: bold !important;
  padding-bottom: 15px;
}

h3 {
  font-size: 1.3em !important;
  color: #6fb9a8 !important;
  font-weight: bold !important;
  padding-top: 10px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.indPoints {
  font-size: 1.4em !important;
}

.Points {
  font-weight: bold;
  color: #61dafb;
}

.prev {
  margin-right: 1vh;
}

.next {
  margin-left: 1vh;
}

.checkbox {
  margin-bottom: 0.5vh;
}

.buttonGroup {
  margin-bottom: 2vh;
}

.btnDescription {
  margin: 5px;
  width: 20px;
  background: #6fb9a8;
  height: 20px;
  text-align: center;
  border-radius: 20px;
  line-height: 15px;
  color: white;
  font-weight: bold;
  font-size: 1em;
  padding: 4px;
  float: right;
  border-width: 0;
  border: none;
}
.Cumule {
  font-size: 1, 5em;
}

.ScoreEquipe {
  font-size: 2em;
  margin-top: 80px;
}

.Marquage {
  color: #6fb9a8;
  font-weight: bold;
}

.Encars {
  width: 100%;
  margin: 0 auto;
  background-color: #e4e4e4;
  border-radius: 3px;
  padding: 10px;
}

.image1 img {
  width: 100%;
  height: 25%;
  /* filter: grayscale(100%); */
}

.image2 img {
  width: 100%;
  height: 25%;
  /* filter: grayscale(100%); */
}

.image3 img {
  width: 100%;
  height: 25%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.4;
}

.image4 img {
  width: 100%;
  height: 25%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.4;
}

.arbre {
  height: 100%;
  width: 100%;
}

.Team {
  margin-bottom: 80px;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.mynavbar {
  color: black !important;
  font-size: 1.8em;
  font-family: "Questrial";
  width: 80%;
  margin: 0 auto;
  top: 20px;
  margin-bottom: 50px;
}

.mynavbar a {
  color: black !important;
  outline: none !important;
}

.mynavbar a:hover {
  -webkit-text-decoration: underline #6fb9a8;
          text-decoration: underline #6fb9a8;
}

.activeLien {
  -webkit-text-decoration: underline #6fb9a8;
          text-decoration: underline #6fb9a8;
}

.navigation {
  width: 70%;
  margin: 0 auto;
}

.itemMenu {
  padding-left: 10vmin;
  outline: none !important;
}

.logohome {
  width: 15%;
  height: auto;
}

.logohome img {
  width: 100%;
  height: auto;
}

.TitreBadge {
  margin-top: 2%;
}

.badges {
  justify-content: space-between;
  display: flex;
}

.success {
  width: 80%;
  margin: 0 auto;
}

.badgeItem {
  float: left;
  text-align: center;
}

.badgeItem img {
  width: 100px;
  height: auto;
  margin-top: 25%;
}

.badgeItemGris {
  float: left;
  text-align: center;
  opacity: 0.2;
}

.badgeItemGris img {
  width: 100px;
  height: auto;
  margin-top: 25%;
  filter: grayscale(1);
  -webkit-filter: grayscale(1);
}

.badgeItem2 img {
  width: 100px;
  height: auto;
}

.reussites {
  margin-top: 60px;
  bottom: 40px;
}

.circle {
  margin: 20px;
  width: 50px;
  background: #6fb9a8;
  height: 50px;
  text-align: center;
  border-radius: 100px;
  line-height: 40px;
  color: white;
  font-weight: bold;
  font-size: 1.5em;
  padding: 5px;
  float: left;
}

.reussiteItem {
  width: 100%;
  float: left;
}

.reussiteText {
  float: left;
  height: 50px;
  padding-top: 35px;
}

